import { createStore } from "vuex";
import { State } from "@/interfaces/State";
import appVersion from "@/util/version";

const STORAGE_KEY = "boto";
const AUTH_TOKEN_KEY = "boto-auth-token";
const LOCALE_KEY = "boto-locale";
const TEMPLATE_KEY = "boto-template";
const APP_VERSION_KEY = "boto-version";
const defaultLocale = "en";

export const store = createStore({
  strict: process.env.NODE_ENV === "development",
  state: {
    authToken: localStorage.getItem(AUTH_TOKEN_KEY) || "",
    locale: localStorage.getItem(LOCALE_KEY) || defaultLocale,
    template: localStorage.getItem(TEMPLATE_KEY) || "",
  },
  getters: {
    localStorageState: () => {
      return JSON.parse(localStorage.getItem(STORAGE_KEY) || "{}");
    },
    localStorageLocale: () => {
      return localStorage.getItem(LOCALE_KEY) || "";
    },
    localStorageAuthToken: () => {
      return localStorage.getItem(AUTH_TOKEN_KEY) || "";
    },
    localStorageTemplate: () => {
      return localStorage.getItem(TEMPLATE_KEY) || "";
    },
    localStorageAppVersion: () => {
      return localStorage.getItem(APP_VERSION_KEY) || "";
    },
  },
  mutations: {
    initialize: (state: State, initialState) => {
      const localVersion = localStorage.getItem(APP_VERSION_KEY);
      const localCfg = localStorage.getItem(STORAGE_KEY);
      if (!localVersion || localVersion !== appVersion || !localCfg) {
        state = initialState;
        localStorage.setItem(STORAGE_KEY, JSON.stringify(initialState));
        localStorage.setItem(APP_VERSION_KEY, appVersion);
      }
    },
    setAuthToken: (state: State, token: string) => {
      state.authToken = token;
      localStorage.setItem(AUTH_TOKEN_KEY, token.toString());
    },
    setTemplate: (state: State, template: string) => {
      state.template = template;
      localStorage.setItem(TEMPLATE_KEY, template.toString());
    },
    removeTemplate() {
      localStorage.removeItem(TEMPLATE_KEY);
    },
    saveLocale: (state: State, locale: string) => {
      state.locale = locale;
      localStorage.setItem(LOCALE_KEY, locale);
    },
  },
});
