import { createApp } from "vue";
import App from "@/modules/App/index.vue";
import { Skeletor } from "vue-skeletor";
import { store } from "./store";
import routes from "./routes";
import { RouteLocationRaw } from "vue-router";
import { setupAuth } from "@/auth/Auth";
import VueNumberInput from "@chenfengyuan/vue-number-input";
import vfmPlugin from "vue-final-modal";
import VueToast from "vue-toast-notification";
import { VTooltip } from "v-tooltip";

function callbackRedirect(appState: { targetUrl: RouteLocationRaw }) {
  routes.push(
    appState && appState.targetUrl ? appState.targetUrl : `${process.env.VUE_APP_CONTEXT}login`
  );
}

const authConfig = {
  domain: process.env.VUE_APP_AUTH0_DOMAIN ? process.env.VUE_APP_AUTH0_DOMAIN : "",
  client_id: process.env.VUE_APP_AUTH0_CLIENT_ID ? process.env.VUE_APP_AUTH0_CLIENT_ID : "",
  redirect_uri: `${process.env.VUE_APP_BASE_URL}/callback`,
  audience: process.env.VUE_APP_BASE_URL,
};

setupAuth(authConfig, callbackRedirect).then((auth: any) => {
  createApp(App, {
    provide: {
      globalVariable: 123,
    },
  })
    .use(store)
    .use(auth)
    .use(store)
    .use(routes)
    .use(vfmPlugin)
    .use(VueToast)
    .directive("tooltip", VTooltip)
    .component(Skeletor.name, Skeletor)
    .component("modal", {
      template: "#modal-template",
    })
    .component(VueNumberInput.name!, VueNumberInput)
    .mount("#app");
});
